@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;0,800;1,400;1,700;1,800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;700;800&display=swap");
@import url("https://fonts.googleapis.com/css?family=Material+Icons%7CMaterial+Icons+Outlined%7CMaterial+Icons+Two+Tone%7CMaterial+Icons+Round%7CMaterial+Icons+Sharp");

@layer components {
  .ccIndicators {
    @apply absolute bottom-0 flex w-full justify-center pt-6;
  }
  .ccBullets {
    @apply cursor-pointer block mr-2 w-4 h-4 rounded-[50%] bg-neutral-600;
  }
  .ccBulletsActive {
    @apply rounded-[60px] w-8;
  }
  .slides {
    @apply flex w-full h-full;
  }
}
.material-icons-outlined {
  font-size: unset;
  line-height: unset;
}
